<template>
  <div class="VimeImage">
       <div class="image">
            <el-carousel  class="el-carousel" :autoplay="false" indicator-position="none" :initial-index="id">
                <el-carousel-item class="el-carousel-item" v-for="(car,index) in banner" :key="index">
                    <img :src="car">
                </el-carousel-item>
            </el-carousel>
             <button type="button" class="el-carousel__arrow el-carousel__arrow--right arrow" @click="goBack"><i class="el-icon-arrow-left"></i><span>返回</span></button>
        </div>
  </div>
</template>

<script>
export default {
  name: 'VimeImage',
  data () {
    return {
        banner:[],
        id: parseInt(this.$route.query.id),
    }
  },
  methods:{
        requestImg(){
          for(var i = 0;i<77;i++){
             this.banner.push(require("../assets/move/"+(i+1)+"-m.jpg"))
             
          }
       },
      goBack(){
         this.$router.push({path:'/Move'});
      }

  },
  mounted(){
    window.scrollTo(0,200);
    this.requestImg();
  }
}
</script>

<style lang="less" scoped>
.VimeImage{
    width: 100%;
    height: 100%;
    background: #000000;
    
    .arrow{
        position: absolute;
        top: 95%;
        left:85%;
        width: 200px;
        opacity: 0.6;
    font-size: 30px;
    color: #ffffff;
    background: none;
    }
}
.image{
     position: relative;
      width: 1170px;
      margin: 0 auto;
      background: #000000;
      margin-bottom: 20px;
  }
  .el-carousel__item{
      img{
          width: 100%;
          height: 100%;
          cursor: pointer;
      }
  }
</style>



